"use client";

import { useCallback, useEffect, useState } from "react";
import { redirect } from "next/navigation";
import { getSessionId } from "../session-management";

// User roles following ADR-011
export enum UserRole {
  ADMIN = "admin",
  DEVELOPER = "developer",
  OBSERVER = "observer",
  SERVICE = "service",
}

// User interface
export interface User {
  id: string;
  email: string;
  name: string;
  role: UserRole;
  tenantId: string;
  agentLimit: number;
  coalitionLimit: number;
  isActive: boolean;
  lastLoginAt?: Date;
}

// Authentication state
export interface AuthState {
  user: User | null;
  isAuthenticated: boolean;
  isLoading: boolean;
  error: string | null;
}

// Permission definitions following ADR-011
export const PERMISSIONS = {
  agent: {
    create: [UserRole.ADMIN, UserRole.DEVELOPER],
    read: [UserRole.ADMIN, UserRole.DEVELOPER, UserRole.OBSERVER],
    update: [UserRole.ADMIN, UserRole.DEVELOPER],
    delete: [UserRole.ADMIN],
    export: [UserRole.ADMIN, UserRole.DEVELOPER],
  },
  coalition: {
    create: [UserRole.ADMIN, UserRole.DEVELOPER],
    read: [UserRole.ADMIN, UserRole.DEVELOPER, UserRole.OBSERVER],
    update: [UserRole.ADMIN, UserRole.DEVELOPER],
    delete: [UserRole.ADMIN],
    business_data: [UserRole.ADMIN],
  },
  system: {
    users: [UserRole.ADMIN],
    metrics: [UserRole.ADMIN, UserRole.DEVELOPER],
    logs: [UserRole.ADMIN],
    backup: [UserRole.ADMIN],
  },
} as const;

// Authentication client
export class AuthClient {
  private baseUrl: string;
  private user: User | null = null;

  constructor(baseUrl: string = "/api/auth") {
    this.baseUrl = baseUrl;
  }

  async getCurrentUser(): Promise<User | null> {
    try {
      const response = await fetch(`${this.baseUrl}/me`, {
        method: "GET",
        credentials: "include",
        headers: { "Content-Type": "application/json" },
      });

      if (response.status === 401) return null;
      if (!response.ok) throw new Error(`HTTP ${response.status}`);

      const data = await response.json();
      this.user = data.user;
      return this.user;
    } catch (error) {
      console.error("Failed to get current user:", error);
      return null;
    }
  }

  hasPermission(
    user: User | null,
    resource: keyof typeof PERMISSIONS,
    action: string,
  ): boolean {
    if (!user || !user.isActive) return false;
    const resourcePermissions = PERMISSIONS[resource] as unknown as Record<
      string,
      UserRole[]
    >;
    const requiredRoles = resourcePermissions[action];
    if (!requiredRoles) return false;
    return requiredRoles.includes(user.role);
  }
}

export const authClient = new AuthClient();

// Client-side authentication guard hook
export function useAuth() {
  const [authState, setAuthState] = useState<AuthState>({
    user: null,
    isAuthenticated: false,
    isLoading: true,
    error: null,
  });

  const checkAuth = useCallback(async () => {
    try {
      setAuthState((prev) => ({ ...prev, isLoading: true, error: null }));
      const user = await authClient.getCurrentUser();
      setAuthState({
        user,
        isAuthenticated: !!user,
        isLoading: false,
        error: null,
      });
    } catch (error) {
      setAuthState({
        user: null,
        isAuthenticated: false,
        isLoading: false,
        error: error instanceof Error ? error.message : "Authentication failed",
      });
    }
  }, []);

  const hasPermission = useCallback(
    (resource: keyof typeof PERMISSIONS, action: string) => {
      return authClient.hasPermission(authState.user, resource, action);
    },
    [authState.user],
  );

  useEffect(() => {
    checkAuth();
  }, [checkAuth]);

  return { ...authState, hasPermission };
}

// Route protection guard for pages
export async function requireAuth(requiredRoles?: UserRole[]): Promise<User> {
  const sessionId = getSessionId("auth");
  if (!sessionId) redirect("/login");

  try {
    const response = await fetch("/api/auth/validate", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ sessionId }),
    });

    if (!response.ok) redirect("/login");

    const data = await response.json();
    const user = data.user;

    if (!user || !user.isActive) redirect("/login");
    if (requiredRoles && !requiredRoles.includes(user.role))
      redirect("/unauthorized");

    return user;
  } catch {
    redirect("/login");
  }
}

// Permission-based component wrapper
interface RequirePermissionProps {
  resource: keyof typeof PERMISSIONS;
  action: string;
  children: React.ReactNode;
  fallback?: React.ReactNode;
}

export function RequirePermission({
  resource,
  action,
  children,
  fallback = null,
}: RequirePermissionProps) {
  const { hasPermission } = useAuth();
  return hasPermission(resource, action) ? <>{children}</> : <>{fallback}</>;
}
